<template>
  <div id="help">

    <div class="help">
      <Nav page="help" />
      <!-- 正文内容 -->

      <Banner text="使用帮助"/>

      <div class="space40"></div>


      <div class="mainContent">



        <div class="shadownItemBox itemBox" style="min-height:300px;">

          <div class="back flex align" @click="goback"> <i class="el-icon-back"></i>&nbsp;&nbsp;<span>返回</span></div>

          <div class="space30"></div>

          <div v-loading="load">

            <div class="textBox animated fadeIn" v-if="content">
              <h1>{{content.title}}</h1>
              <div class="small font12 op04">{{content.addTime}} 创建</div>

              <div class="space20"></div>

              <hr>

              <div class="htmlContent" v-html="content.contentText"></div>
            </div>
          </div>
          <!-- loadBox -->

        </div>
        <!-- itemBox -->

      </div>
      <!-- mainContent -->

      <!-- 正文结束 -->

    </div>
    <!-- filter -->

    <div class="space40"></div>

    <Footer />


    <BuyForm v-if="showForm" @hide="showForm=false" />


  </div>
  <!-- index -->
</template>

<script>
  import {
    noticeDetail
  } from '../data/apis';

  import Nav from './nav'
  import Footer from './footer'
  import Banner from '@com/exchange/banner'


  export default {
    data() {
      return {
        content: null,
        load: false
      }
    },
    components: {
      Nav,
      Footer,
      Banner
    },
    methods: {
      getData() {
        this.load = true;
        noticeDetail({
          id: this.$route.query.id
        }).then(v => {
          this.content = v.data;
          this.load = false;
        })
      } //getData
    },
    mounted() {
      this.getData();
    } //mounted
  };

</script>
<style rel="stylesheet/scss" scoped lang="scss">
  @import "../styles/Val";

  .help {
    min-height: calc(100vh - 200px);
  }

  .itemBox {
    .textBox {
      min-height: 200px;
    }

    .back {
      cursor: pointer;
      color: $them_color_gray;

      span {
        font-size: 20px;
      }

      i {
        font-size: 30px;
      }

      &:hover {
        * {
          color: $them_color;
        }
      }
    }

    >div {
      h1 {
        font-size: 25px;
        display: block;
        font-weight: bold;
      }

      .htmlContent {
        line-height: 2em;
        font-size: 14px;

        img {
          max-width: 100% !important;
        }
      }
    }
  }

</style>



